.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 60px;
    background:#40E0D0;
    padding: 0% 13%;
    position:sticky;
    z-index: 1;
    top:0;
}
.navlinks#hidden{
    display: flex;
    flex-direction: column;
}

.logo a{
    color:white;
    font-size:34px;
}
.logo a>p{
    font-size: 15px;

}
.navlinks{
    display: flex;
    list-style: none;
    
}

.navlinks a{
    color: white;
    margin-left: 10px;
}
.menu{
    display: none;
    font-size:25px;
    color: white;
    
   
}

@media only screen and (max-width: 600px) {
  .navbar{
    padding: 0 15px;
    width: 100%;
  }
.links{
    display: none;
}
.links#hidden{
    display: flex;
    position: absolute;
    top: 60px;
    background-color: black;
    width: 100%;
    left: 0;
    height: 210px;
    padding-top: 3rem;
    
   
}
.navlinks{
    display: flex;
    flex-direction: column;
    margin-left: 15px;
}
  
 
  .navlinks a{
    margin: 5px 0;
  }
 
  .menu{
    display:block;
  }
 
}