.home{
    width: 100%;
    overflow-y: scroll;
    overflow: hidden;
    
}

.hero{
   
    height:calc(70vh - 60px);
    width: 100%;
}
.rightSide{
     background:linear-gradient(to left, #32ccbd, #fefdfb) 50%;
     height: inherit;
     width: 80%;
     right: 0;
     position: absolute;
     opacity: 0.8;
     
}

.body h1{
    font-size: 50px;
}
.card{
    width:100%;
    padding: 0% 13%;
}

.cardTitle{
    padding-right: 10px;
}
.cardInfo{
   width:60%;
   margin-left: auto;


}
.cardInfo>p{
    text-align: left;
}
/* ----beggining services section----*/
.Services{
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top: 5rem;
    padding: 0% 13%;
    

}
/*services section color pallets*/
.servicesBox{
   display: grid;
   grid-template-columns:repeat(3,1fr);
   gap: 10px;
   margin-top: 2rem;
   width: 100%;
}
.box{
 width:310px;
 margin: 10px;
 height: 130px;
background-color: rgb(246, 244, 244);
}
.box h1{
    font-size: 15px;
    padding-top: 5rem;
}

/*---end services section----*/


/*-----WHO ARE WE SECTION---*/
.whoCard{
    display: flex;
    flex-direction: column;
   
    height: 15rem;
    margin-top: 4rem;

}


/*-----END WHO ARE WE-----*/
/* ----WHY US SECTION-------*/
.usCard{
    display: flex;
    flex-direction: column;
    text-align: right;
    height:15rem;
    
    margin-top: 4rem;
}
.usInfo{
    width: 50%;
}
.usInfo>p{
    overflow: hidden;
    text-align: left;
}
/*---END WHY US CARD----------*/
/*BEGIN TIPS CARD-------*/
.tipsCard{
    margin-top: 4rem;
    display: flex;
    flex-direction: column;
    text-align:center;
    height:20rem;
    
    
}
.tipsBody{
    display: flex;
    flex-direction: row;
}
.tip{
    width: 15rem;
    height: 15rem;
  
    margin: 15px;
    
}
/*------END TIPS----*/
/*-------MEDIA SCREEN----*/
@media only screen and (max-width: 600px) {
  .hero{
    margin-bottom: 2rem;
  }
  
    .Services{
    display: flex;
    padding: 0;
    margin: 2.5rem 0 0 0 ;
    width: 100%;
    text-align: center; 
    justify-content: center;   
  }
  
.servicesBox{
      display: block;
      padding: 0 15px;
  }
  .box{
     width:100%;
     margin: 15px 0;
     padding: 0;
  }
  .card{
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
    text-align: center;
    align-items: center;
  }
.cardTitle{
    padding-right: 10px;
}
.cardInfo{
   margin: 0;
   width: 100%;



}
.cardInfo>p{
    text-align: center;
    width:100%;
   
    margin:0;
    padding: 0;
}

    
  
  .usCard{
    padding: 0;
    margin: 0;
  
    text-align: center;
    margin: 1rem;
    
  }
  .usInfo{
    width: 100%;
  }
  .usCard p{
    text-align: center;
    width: 100%;
   
  }
  .whoCard{
    margin: 1rem ;
    padding: 0;
    text-align: center;
    align-items: left;
    
  }
}