
.footer{
    width: 100%;
    margin-top: 4rem;
    height: 50vh;
    position: sticky;
    bottom: 0;
    background-color: rgb(246, 244, 244);
    padding: 3% 13%;
}
.footerheader{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    color:rgb(131, 127, 127) ;
}
.footerlogo a{
    color:rgb(131, 127, 127) ;
}
.footerlogo p{
    font-size: 8px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.catCard{
    list-style: none;
    

}

.catBody{
    list-style: none;
    font-size: 15px;
    margin-top: 1.2rem;
    line-height: 20px;
    
}
.locationCard{
  margin-right: -1rem;
}
.locationCard img{
    height: 500px;
    width: 100%;
    
}
.locBody{
    margin-top: 1.2rem;
}
.contactsCard h1{
    margin-bottom: 1rem;
}
.footerEnd{
    height:50px;
    background-color:#40E0D0;
    width: 100%;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.footerIcons{
    display: flex;
    position: absolute;
    bottom:0;
    padding: 1.5rem 0rem;
    border-top: 1px solid rgb(165, 164, 164);
    width:75.3%;
    text-align: center;

}

.footerIcons h4{
    color:  rgb(165, 164, 164);
    font-size: 14px;
    margin-right: 3rem;
}
.footerIcons a{
    border: 1px solid rgb(230, 227, 227);
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: flex ;
    align-items: center;
    justify-content: center;
    margin-right: 25px;

    
}
.footerIcons img{
    width:20px;
    height: 20px;
    padding: 2px;

}
@media only screen and (max-width: 600px) {
 .footer{
    width: 100%;
    margin-top: 4rem;
    height: 100%;
    bottom: 0;
    padding: 0 15px;
    
    
}
.footer h1{
    margin:1rem 0;
}
.footerheader{
    display: block;
    padding: 2rem 0rem 7rem;
    
}
.footerIcons{
    display: block;
    text-align: left;
    border: none;
   
    

    
    
}
.footerIcons h4{
    margin-bottom: 15px;
}
}